														class="form-control"
														id="userFirstname">
												</div>
												<div class="invalid-feedback">
													{{ errors.fname }}
												</div>
											</div>
											<div class="form-group col-md-6">
												<div class="settings__value">
													<label for="userLastname">Last Name</label>
													<input
														:class="[{'is-invalid': errors.lname}]"
														@keydown="clearErrors($event.target.name)"
														v-model="itemData.lname"
														value=""
														name="lname"
														type="text"
														class="form-control"
														id="userLastname">
												</div>
												<div class="invalid-feedback">
													{{ errors.lname }}
												</div>
											</div>
										</div>
									</div>
									<div class="form-group">
										<div class="settings__value">
											<label for="userEmail">Email Address</label>
											<input
												:class="[{'is-invalid': errors.email}]"
												@keydown="clearErrors($event.target.name)"
												v-model="itemData.email"
												name="email"
												value=""
												type="email"
												class="form-control"
												id="userEmail"
												disabled="disabled"
												placeholder="Email Address">
										</div>
										<div class="invalid-feedback">
											{{ errors.email }}
										</div>
									</div>
									<div class="form-group">
										<div class="settings__value">
											<label for="userPhone">Phone Number</label>
											<input
												:class="[{'is-invalid': errors.phone}]"
												@keydown="clearErrors($event.target.name)"
												v-model="itemData.phone"
												name="phone"
												value=""
												type="phone"
												class="form-control"
												id="userPhone"
												disabled="disabled"
												placeholder="Phone Number">
										</div>
										<div class="invalid-feedback">
											{{ errors.phone }}
										</div>
									</div>

									<div class="form-row">
										<div class="form-group col-md-6">
											<div class="settings__value">
												<label for="category">ACL</label>
												<select 
													class="custom-select form-control"  
													id="category"
													@change="changeAcl"
													v-model="selectedAcl"
													:class="[{'is-invalid': loadingAcl}]">
													<option value="" select>Change ACL</option>
													<template v-if="aclLoaded">
														<option 
														v-for="item in acl" 
														:key="item.id"
														:value="item.id">{{item.name}}</option>
													</template>
													<template v-else>
														<option>Something went wrong! Refresh page</option>
													</template>
													<div class="invalid-feedback">
														{{ errors.acl_id }}
													</div>
												</select>
											</div>
										</div>
									</div>
								</form>
							</div>
						</div>

						<div class="tab-pane fade user-profile__tabs__section" id="credentials" role="tabpanel" aria-labelledby="credentials-tab">
							<div class="user-profile__notification">
								<div class="user-profile__notification__item">
									<section class="main-grid__row contacts">
										<div class="contacts__wrapper">
											<template v-if="credentials.length">
												<div 
													v-for="credential in credentials"
													:key="credential.id"
													:credential="credential"
												>
													<div class="contacts__card">
														<div class="contacts__info">
															<div class="contacts__img">
																<img :src="credential.document" alt="">
															</div>
															<div class="info__primary">
																<h6 class="info__name">{{credential.title}}</h6>
																<button
																	v-if="getLoggedUser.category !== 'employer'"
																	@click.prevent="toggleUserCredential(credential)"
																	class="btn btn--sm"
																	:class="isUnapprove(credential.status)"
																	type="button"
																	data-dismiss="modal"
																	aria-label="close"
																	name="button">
																	{{ credential.status === 1 ?
																		'Unapprove' :
																		'Approve'
																	}}
																</button>
																<button 
																	@click="selectCredential(credential)"
																	class="btn btn__outline btn__outline--sm" 
																	type="button" 
																	name="button" 
																	data-toggle="modal" 
																	data-target="#showCredentialModal">
																	View
																</button>
															</div>
														</div>
													</div>
												</div>
											</template>
											<template v-else>
												No credentials available.
											</template>
										</div>
									</section>
								</div>
							</div>
						</div>

						<div class="tab-pane fade user-profile__tabs__section" id="classes" role="tabpanel" aria-labelledby="classes-tab">
							<div class="user-profile__notification">
								<div class="user-profile__notification__item">
									<section class="main-grid__row contacts">
										<div class="contacts__wrapper">
											<template v-if="itemData.classes.length">
												<div 
													v-for="item in itemData.classes"
													:key="item.id"
												>
													<div class="contacts__card">
														<div class="contacts__info">
															<div class="contacts__img">
																<img :src="item.class.avatar" alt="">
															</div>
															<div class="info__primary">
																<h6 class="info__name">{{item.class.title}}</h6>
																<router-link
																	:to="{
																		name: 'class-detail',
																		params: { id: item.class_id }
																	}"
																>
																	<button 
																		class="btn btn__outline btn__outline--sm" 
																		type="button" 
																		name="button" >
																		View
																	</button>
																</router-link>
															</div>
														</div>
													</div>
												</div>
											</template>
											<template v-else>
												No classes available.
											</template>
										</div>
									</section>
								</div>
							</div>
						</div>

						<div class="tab-pane fade user-profile__tabs__section" id="certificates" role="tabpanel" aria-labelledby="certificates-tab">
							<div class="user-profile__notification">
								<template v-if="showCertificate">				
									<CertificateModal
										:title="certificateData.title"
										:score="certificateData.score"
										:fullName="certificateData.fullName"
									/>
									<div class="btn--wrap">
										<button
											@click="{showCertificate = false}"
											type="button" 
											class="btn btn__green">
											Back
										</button>
									</div>
								</template>
								<template v-else>
									<div class="user-profile__notification__item">
											<section class="main-grid__row contacts">
												<div class="contacts__wrapper">
													<template v-if="certificates.length">
														<div 
															v-for="certificate in certificates"
															:key="certificate.id"
														>
															<div class="contacts__card">
																<div class="contacts__info">
																	<div class="contacts__img">
																		<img :src="certificate.class.avatar" alt="">
																	</div>
