																		<h6 class="info__name">{{certificate.class.title}}</h6>
																		<button 
																			@click="setCertificateData(certificate)"
																			class="btn btn__outline btn__outline--sm" 
																			type="button" 
																			name="button" >
																			View Certificate
																		</button>
																	</div>
																</div>
															</div>
														</div>
