			</section>
		</template>
		<CredentialModal :credential=selectedCredential />
	</div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';
import { 
	getUserProfile,
	updateUser,
	uploadAvatar,
	getCareerCategories,
	getUserCertificates,
	updateUserCredential
} from '../../utils/api/users';
import Loader from '../../components/Loader';
import user from '../../utils/validations/user';
import form from '../../mixins/form';
import { success, fail } from '../../utils/toast';
import CredentialModal from './CredentialModal';
import eventHub from '../../utils/eventhub';
import CertificateModal from './CertificateModal';
import { getAcl } from '../../utils/api/acl';

export default {
	name: 'UserProfile',
	mixins: [form],
	data() {
		return {
			itemData: {},
			user: [],
			loadingUser: true,
			userLoaded: false,
			errors: {},
			avatar: '',
			avatarUploaded: false,
			isUploadingAvatar: false,
			selectedCategory: '',
			selectedAcl: '',
			categories: [],
			loadingCategories: false,
			categoriesLoaded: false,
			credentials: [],
			credentialItem: {},
			selectedCredential: {},
			certificates: [],
			certificatesLoaded: false,
			showCertificate: false,
			loadingAcl: true,
			aclLoaded: false,
			acl: []
		};
	},
	watch: {
		// eslint-disable-next-line
		'$route' (to, from) {
			this.getUserProfile();
		}
	},
	mounted() {
		this.getAcl();
	},
	created() {
		this.getUserProfile();
		eventHub.$on('credential-selected', (payload) => {
			this.selectedCredential = payload;
		});
		this.getUserCertificates();
	},
	methods: {
		...mapMutations([
			'updateLoggedUser'
		]),
		getAcl() {
			getAcl()
				.then((response) => {
					if (response.status === 200) {
						this.aclLoaded = true;
						this.acl = response.data.data;
						return true;
					}
					this.aclLoaded = false;
					return false;
				})
				.catch((error) => {
					this.aclLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingAcl = false;
				});
			return true;
		},
		updateCareerCategory(data) {
			updateUser(data)
				.then((response) => {
					if (response.status === 200) {
						if (this.selectedCategory) {
							this.itemData.category = this.categories.find((item) => {
								return item.id === this.selectedCategory;
							});
							this.categoryItem.name = this.itemData.category.name;
						}
						success({
							title: 'Success! Account has been updated!'
						});
						return Promise.resolve(response);
					}
					this.loading = false;
					return false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		getCategories() {
			this.loadingCatgories = true;
			getCareerCategories()
				.then((response) => {
					if (response.status === 200) {
						this.categories = response.data.data;
						this.categoriesLoaded = true;
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.categoriesLoaded = false;
					this.loadingCatgories = false;
					return error;
				});
			return true;
		},
		changeCategory() {
			this.itemData.career_category_id = this.selectedCategory;
		},
		changeAcl() {
			this.itemData.acl_id = this.selectedAcl;
		},
		/**
		* Uploads user avatar
		*/
		uploadAvatar(file) {
			this.isUploadingAvatar = true;
			this.avatarUploaded = false;
			uploadAvatar(this.$route.params.id, file)
				.then((response) => {
					if (response.status === 200) {
						this.itemData.avatar = file;
						this.updateLoggedUser({ avatar: file });
						this.avatarUploaded = true;
						return true;
					}
					this.avatarUploaded = false;
					return false;
				}).catch((error) => {
					this.avatarUploaded = false;
					return error;
				}).finally(() => {
					this.isUploadingAvatar = false;
				});
			return true;
		},
		/**
		* Checks for image format and if image size < 3MB
		*/
		changeAvatar() {
			const img = this.$refs.avatar.files[0];
			const imageSize = 3 * (2 ** 20);
			if (img && img.type.match(/image\/*/)) {
				if (img.size > imageSize) {
					errors.avatar = 'File size greater than 3MB';
					return false;
				}
			}
			const reader = new FileReader();
			reader.onload = (e) => {
				this.avatar = e.target.result;
				this.uploadAvatar(this.avatar);
			};
			reader.readAsDataURL(img);
			return true;
		},
		getUserProfile() {
			getUserProfile(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						const result = response.data.data;
						delete result.created_at;
						delete result.updated_at;
						this.itemData = result;
						this.itemData.avatar = result.avatar;
						this.credentials = result.credentials;
						this.classes = result.classes;
						return true;
					}
					this.userLoaded = false;
					return false;
				}).catch((error) => {
					this.userLoaded = false;
					return error;
				}).finally(() => {
					this.loadingUser = false;
				});
		},
		updateUser() {
			/**
			 * Validate input
			 */
			const {
				fname,
				lname,
				id
			} = this.itemData;
			const result = {
				fname,
				lname,
				id,
				acl_id: this.itemData.acl_id
			};
			this.validate(result, user.update);
			if (Object.keys(this.issues).length > 0) {
				this.errors = this.issues;
				return false;
			}
			updateUser(result)
				.then((response) => {
					if (response.status === 200) {
						if (this.selectedCategory) {
							this.itemData.category = this.categories.find((item) => {
								return item.id === this.selectedCategory;
							});
							this.categoryItem.name = this.itemData.category.name;
						}
						success({
							title: 'Success! Account has been updated!'
						});
					}
					this.loading = false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		toggleUserStatus() {
			/**
			 * Delete everyting from the object.
			 */
			if (this.itemData.active === 1) {
				this.itemData.active = 0;
			} else {
				this.itemData.active = 1
			}
			const { active, id } = this.itemData;
			const result = { active, id };
			updateUser(result)
				.then((response) => {
					if (response.status === 200) {
						const message = response.data.active == 1
							? 'Account has been activated'
							: 'Account has been suspended';
						success({
							title: `Success! ${message}`
						});
					}
					this.loading = false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		toggleUserCredential(value) {
			for (let i = 0; i < this.credentials.length; i += 1) {
				this.credentialItem = this.credentials[i];
				if (this.credentialItem.id === value.id) {
					this.credentialItem.status = this.credentialItem.status === 0 ? 1 : 0;
					updateUserCredential(this.itemData.id, this.credentialItem)
						.then((response) => {
							if (response.status === 200) {
								const message = this.credentialItem.status === 1
									? 'Credential has been approved'
									: 'Credential has been unapproved';
								success({
									title: `Success! ${message}`
								});
							}
							this.loading = false;
							return true;
						})
						.catch((error) => {
							this.loading = false;
							fail({
								title: 'Ooops! Something went wrong. Pls try again.'
							});
							return error;
						});
					return true;
				}
			}
			return false;
		},
		selectCredential(item) {
			eventHub.$emit('credential-selected', item);            
		},
		toggleAdminStatus() {
			this.itemData.admin = this.itemData.admin === 0 ? 1 : 0;
			this.updateCareerCategory({ admin: this.itemData.admin, id: this.$route.params.id });
		},
		getUserCertificates() {
			getUserCertificates(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.certificates = response.data.data;
						this.certificatesLoaded = true;
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.certificatesLoaded = false;
					return error;
				});
			return true;
		},
		setCertificateData(data) {
			this.certificateData = {};
			this.certificateData.title = data.class.title;
			this.certificateData.score = data.score;
			this.certificateData.fullName = this.fullName;
			this.showCertificate = true;
		},
		isUnapprove(status) {
			return [
				status === 1
					? 'btn__danger'
					: 'btn__save'
			];
		}
	},
	computed: {
		...mapGetters([
			'getLoggedUser'
		]),
		fullName() {
			return `${this.itemData.fname} ${this.itemData.lname}`;
		},
		isSuspend() {
			return [
				this.itemData.active === 1
					? 'btn__danger'
					: 'btn__save--outline'
			];
		},
		isActive() {
			return [
				this.itemData.active === 1
					? 'badge badge-pill badge-success'
					: 'badge badge-pill badge-danger'
			];
		},
		isSelf() {
			return this.itemData.id === this.$session.get('id');
		},
		categoryItem() {
			return this.itemData.category;
		}
	},
	components: {
		Loader,
		CredentialModal,
		CertificateModal
	}
};
</script>

